*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
}
body {
  background-color: #DDD;
}
.App{
  display: flex;
  align-items: center;
  justify-content: center;
  min-height: 100vh;
}
.c-player{
  display: block;
  background: linear-gradient(to bottom, rgba(255,255,255,0.15) 0%, rgba(0,0,0,0.15) 100%), radial-gradient(at top center, rgba(255,255,255,0.40) 0%, rgba(0,0,0,0.40) 120%) #989898;
 background-blend-mode: multiply,multiply;
  padding: 50px;
  border-radius: 160px;
  box-shadow: rgb(38, 57, 77) 0px 20px 30px -10px;
}
.c-player > h4{
  color: #ffffff;
  font-size: 14px;
  text-transform: uppercase;
  font-weight: 400;
  text-align: center;
}
.c-player>p{
  color: #AAA;
  font-size: 14px;
  text-align: center;
  font-weight: 400;
  
}
.c-player--details .details-img{
  position: relative;
  width: fit-content;
  margin: 50px auto;

}
.c-player--details .details-img img{
  display:block;
  margin: 0px auto;
  width: 100%;
  max-width: 250px;
  border-radius: 50%;
  box-shadow: rgba(0, 0, 0, 0.3) 0px 19px 38px, rgba(0, 0, 0, 0.22) 0px 15px 12px;
}
.c-player--details .details-title{
  color: #EEE;
  font-size: 25px;
  text-shadow: 0px 3px 0px black,
  0px 14px 10px rgba(0,0,0,0.15),
  0px 24px 2px rgba(0,0,0,0.1),
  0px 24px 30px rgba(0,0,0,0.1);
  text-align: center;
  margin-bottom: 10px;
}
.c-player--details .details-artist{
  color: #AAA;
  font-size: 20px;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.8), -2px, -2px, 4px rgb(255, 255, 255, 0.4);
  text-align: center;
  margin-bottom: 20px;

}
.c-player--controls{
  display: flex;
  align-items: center;
  justify-content: center;
  margin-bottom: 30px;

} 

.c-player--controls .play-btn{
  
  margin: 0 30px;
  padding:20 px;
  border-radius: 150%;
  box-shadow: rgba(0, 0, 0, 0.56) 0px 22px 70px 4px;
  width: 80px;
  border: none;
  outline: none;
  background-color: black;
  background-size: 200px;
  color: white;
  font-size: 50px;
  cursor: pointer;
}
.c-player--controls .skip-btn{
  background: none;
  border: none;
  outline: none;
  cursor: pointer;
  color: #FFF;
  font-size: 16px;
  box-shadow: rgba(0, 0, 0, 0.56) 0px 22px 70px 4px;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
